body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.aragonCard {
    box-shadow: rgba(0, 0, 0, 0.07) 0px 0px 0px 1px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 75vw;
    border-radius: 2px;
    padding: 30px;
}

.headerContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
}

.headerGroupItem {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.headerInnerItem {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.currentBlockNumberContainer {
    height: 100%;
}

.currentBlockNumber {
    font-size: 1.8em;
    padding: 8px;
    background-image: linear-gradient(130deg,#00B4E6,#00F0E0);
    border-radius: 3px;
}

.timeAgo {
    font-size: 1.4em;
}

@media only screen and (max-width: 1000px) {

    .headerContainer {
        font-size: 0.9em;
        display: flex;
        flex-direction: column;
    }

    .currentBlockNumber {
        font-size: 1.2em;
    }

    .headerInnerItem {
        font-size: 0.8em!important;
        padding: 3px; 
        overflow: hidden;
    }

    .headerGroupItem {
        overflow: hidden;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }
    
    .mobileTopInfo {
        display: grid;
        grid-template-columns: 50% 50%;
        margin-bottom: 10px;
    }

    .mobileTopInfo > div > span {
        font-size: 1.2em;
    }

    .mobileTopInfo > .headerInnerItem > span {
        font-size: 1em;
    }

    .mobileBottomInfo {
        display: grid;
        grid-template-columns: 33% 33% 33%;
    }

}
.TransactionInfoContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 10px;
}

.transactionText {
    font-size: 1.2em;
}

.transactionInnerContainer {
    display: flex;
    flex-direction: column;
}

@media only screen and (max-width: 1135px) {
    .transactionText {
        font-size: 1em;
    }

    div.blockList > div > div > table {
        font-size: 0.8em;
    }

    .TransactionInfoContainer {
        margin-bottom: 10px;
    }
}

@media only screen and (max-width: 550px) {

    .TransactionInfoContainer {
        display: flex;
        flex-direction: column;
        margin-bottom: 10px;
    }

    .mobileEtherValues {
        display: flex;
        flex-direction: row;  
    }
}
.BlockListItem {
    width: 75vw;
    margin-bottom: 10px;
}

.itemBlockHash {
    font-size: 1em;
}

.OuterTableCellContent {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
}

.tableCellContent {
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.etherscanButton {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    padding: 20px;
}

.marginButton {
    margin-right: 5vw;
    margin-left: 10px;
}

.tableContainer .bqABXJ{
    width: 75vw;
    padding: 15px!important;
}

.bWgcJR {
    height: 110px;
    width: 100%;
    overflow-y: scroll; 
}

.DropDown__DropDownItems-sc-17zpefi-1.bWgcJR {
    padding-bottom: 5px;
    height: 130px;
}

.DropDown__StyledDropDown-sc-17zpefi-0.fBoWue {
    width: 100%;
}

@media only screen and (max-width: 1135px) {

    .BlockListItem {
        font-size: 1em;
    }

    .OuterTableCellContent {
        display: flex;
        flex-direction: column;
        align-items: stretch;
    }

    .etherscanButton {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        text-align: center;
        padding: 5px;
    }

    .marginButton {
        margin-right: 0;
        margin-left: 0;
    }

    .tableContainer .bqABXJ{
        width: 75vw;
        padding: 15px!important;
    }
    
    .tableCellContent {
        padding: 0!important;
    }

    .DropDown__DropDownItems-sc-17zpefi-1.bWgcJR {
        padding-bottom: 5px;
        height: 120px;
    }
}

@media only screen and (max-width: 550px) {

    .hideOnMobile {
        display: none;
    }

}
.blockList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
}
.App {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Header {
  margin-bottom: 6vh;
}

.spinnerContainer {
  position: absolute;
  top: 10vh;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1000px) {

  .aragonCard {
    padding: 20px;
  }
}
